:root {
    --main-bg-color:#316cb3;
    --main-text-color: white;
    --component-bg-color: #c5d8ef;
    --component-text-color:#316cb3;
}

img{
    width: 100%;
}

.media{
    grid-area:media;
    padding: 15px;
    border-radius: 25px;
    display: flex;
    align-items: center;
    
}
.projectName{
    grid-area:name;
}
.desc{
    grid-area:desc;
    color: black;
    font-size:20px;
}
.keywords{
    grid-area:keywords;
}
.keyword{
    list-style: square;
    text-align: justify;
    color: black;
    font-family: "Arial", Gadget, sans-serif;
    font-size: 25px;
}
.gitHub{
    grid-area:gitHub;
    max-width: 50px;
}
.length{
    grid-area:length;
    color:black;
    padding-top: 20px;
    text-align: center;
}

@media (max-width:1099px){
    .ProjectCard{
        margin:15px;
        padding: 5px;
        border-radius: 25px;
        background-color: var(--component-bg-color);
        color:var(--component-text-color);
        text-align: center;
    
        display: grid;
        grid-template-columns: 2fr 1fr;
        grid-column-gap: 5px;
        grid-row-gap: 5px;
        grid-template-areas:
            "name name"
            "media gitHub"
            "media length"
            "media keywords"
            "desc desc";
    }

    .media{
        max-width: 60vw;
    }
}

@media (max-width:500px){
    .ProjectCard{
        margin:15px;
        padding: 5px;
        border-radius: 25px;
        background-color: var(--component-bg-color);
        color:var(--component-text-color);
        text-align: center;
    
        display: grid;
        grid-template-columns: 2fr 1fr;
        grid-column-gap: 5px;
        grid-row-gap: 5px;
        grid-template-areas:
            "name name"
            "media media"
            "keywords gitHub"
            "keywords length"
            "desc desc";
    }

    .media{
        max-width: 100vw;
    }
}

@media (min-width:1100px){
    .ProjectCard{
        margin:15px;
        padding: 5px;
        border-radius: 25px;
        background-color: var(--component-bg-color);
        color:var(--component-text-color);
        text-align: center;
    
        display: grid;
        grid-template-columns: 2fr 6fr 1fr 75px;
        grid-template-rows: 1fr 3fr;
        grid-column-gap: 5px;
        grid-row-gap: 5px;
        grid-template-areas:
            "media name length gitHub"
            "media desc keywords keywords";
    }

    .desc{
        font-size: 1.5vw;
    }
}